.avtar-img{
  height: 250px;
  padding-top: 5px;
}
#sahil{
  background: #833ab4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  left: 0;
  width:100%;
  height: 100%;
}
.projects-grid{
  display: flex;

}
.sahil2{
  padding:16px;
}

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}
/* Resume Page css */
.resume-right-col {
  background: #27221F;
  color: white;
  padding: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.header-color{
  background: #833ab4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.banner-text{
  background-color: black;
  opacity: 0.8;
  width:75%;
  margin:auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 50px;
  font-weight: bold;
  color: white;
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;

}

.banner-text p{
  color:white;
  font-size: 20px;
  padding: 1em;
}

.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin:auto;
}

.social-links i{
  color:white;
  font-size: 5em;
  padding: 8px;
}

.con-cla{
  font-size: 30px;
  font-family: Anton;
}

@media screen and (max-width:959px){
  /*landing page*/
  .banner-text{
    width:75%;
  }

  .banner-text h1{
    font-size: 30px;
  }

  .banner-text p{
    font-size: 13px;
    padding: 0.5em;
  }
  .social-links{
    justify-content: center;
  }
  .social-links i{
    font-size: 3em;
    padding: 4px;
  }
  /*contact*/
  .contact-body{
    position: relative;
  }
  .con-cla{
    font-size:17px;
  }
  /*Projects*/

#sahil{
  position: relative;
}
.din{
  
  background: #833ab4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  

}

}